<template>
	<div id="app">
		<div class="back" @touchstart="backTo()">
			<img src="../../assets/img/back.png" />
			<p></p>
		</div>
		<div class="title">
			<p class="title_p"></p>
			<p class="title_tip"></p>
		</div>

		<div class="phone">
			<p></p>
			<input ref="name" type="number" placeholder="" maxlength="11" />
		</div>
		<div class="down"></div>
		<div class="password">
			<p></p>
			<input ref="password" type="password" placeholder="" maxlength="12" />
		</div>
		<div class="new_down"></div>
		<p class="forget" @touchstart="forget"></p>
		<div class="sumbit" @touchstart="login()">
			<p></p>
		</div>
	</div>
</template>

<script>
	import md5 from 'js-md5';
	import store from '../../vuex/store'
	import { Toast } from 'mint-ui';
	export default {
		store,
		data(){
			return{
				id:''
			}
		},
		methods: {
			backTo() {
				this.$router.push({
					path: '../home'
				})
			},
			login() {

				var reg = /^1[3|4|5|7|8][0-9]{9}$/;
				var reg_pass = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,12}$/; //6-12
				var phone = this.$refs.name.value.trim()
				var password = this.$refs.password.value.trim()

				if(phone != null && phone != "") {
					if(reg.test(phone)) {
						if(password != null && password != "") {
							if(reg_pass.test(password)) {
								var m_pass = md5(password);
								var params = "{\"loginType\":\"2\",\"loginAccount\":\"" + phone + "\",\"loginPwd\":\"" + m_pass + "\",\"pid\":\"684589bb5b0e40059ceb41d71be23d8d\",\"rid\":\"631e32975df34e89a6ec9f58512b912f\",\"cpFlag\":\"0\",\"regEquipment\":\"WAP\"}";
								console.log(": " + params);
								this.$http({
									url: this.$store.state.request_url+'/api/user/login.do',
									method: 'post',
									headers: {
										"content-type": "application/json;charset=UTF-8"
									},
									body: params,
									emulateJSON: false,
								}).then(function(response) {

									if(response.body.meta.res === "00000") {
										console.log("memId");
										console.log(response.body.data)
//										this.$store.state.data = response.body.data;
										
										localStorage.memId=response.body.data.memId
                                        localStorage.tokEn=response.body.data.tokEn
                                        localStorage.info=response.body.data.info
										
										/**/
										Toast({
											message: '',
											position: 'middle',
											duration: 2000
										});
										//this.id=response.body.data.opId
//										this.connectWebSocket(response.body.data.memId);//
										
										this.getUserInfo(response.body.data.memId,response.body.data.tokEn);/**/
//										
									} else {
										/**/
										Toast({
											message: response.body.meta.msg,
											position: 'middle',
											duration: 2000
										});
										
									}

								}).catch(function(err) {
									console.log(err)
									/**/
										Toast({
											message: "",
											position: 'middle',
											duration: 2000
										});
								})
							} else {
								/**/
										Toast({
											message: '6-12,',
											position: 'middle',
											duration: 2000
										});
							}

						} else {
							/**/
							Toast({
								message: '',
								position: 'middle',
								duration: 2000
								});
							
						}
					} else {
						/**/
							Toast({
								message: '',
								position: 'middle',
								duration: 2000
								});
							
						
					}
				} else {
					/**/
							Toast({
								message: '',
								position: 'middle',
								duration: 2000
								});
					
				}

			},
			/**/
			connectWebSocket(id){
				//  web socket

				this.ws = new WebSocket("ws://120.76.188.131:6666?opId="+id);
				
			
				/**/
				if(this.ws.readyState != 1) {

				console.log(this.ws)
				
				var that = this
				/**/
				this.ws.onopen = function() {
					console.log("",that.ws)
					that.$store.state.new_ws=that.ws;
					that.ws.onmessage = that.getMessage; /**/
					}
				}
				/**/
				this.ws.onerror = function() {
					alert("...");
				}
				this.ws.send=function(){
					
				}
				
			},
			/**/
			getUserInfo(id,token){
			var url="http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do";
		    var ts=new Date().getTime();
		    var sign = md5(url+id+token+ts)
		        
		  	this.$http({
				url: "http://www.ding-new.com:8080/a10/api/map/user/artUserInfo.do?memId="+id+"&ts="+ts,
				headers: {
					"content-type": "application/json;charset=UTF-8",
					'sIgn': sign
				},
				emulateJSON: false,
				method: 'post',
				body:{
					opId:id
				}
			}).then(function(response) {
				
				if(response.body.meta.res == '00000'){
                   
                   if(response.body.data.nickname==null||response.body.data.nickname==""){
//						window.location="/#/regiter_information"
						this.$router.push({path: '../regiter_information'})
					}else{
						localStorage.opPic=response.body.data.opPic;
						localStorage.nickname=response.body.data.nickname;
						this.$router.push({path: '../slider'})
//						window.location="/#/slider"
					}
				}
			}).catch(function(err){
				
				console.log(err)
				
			 })
			},
			forget(){
				this.$router.push({
						path: '../forget'
						});
			},
			/**/
			getMessage(evt) {
				var received_msg = evt.data;
				console.log(evt.data,"")
				if(received_msg != "," && received_msg != "") {

					//jsonjson
					var json = eval('(' + received_msg + ')');
					console.log(json);
					
					switch(json.msgType){
						case 'msg'://
							
							this.$store.state.is_infro=true;
							this.$store.state.msgs.push(json)  
							console.log(this.$store.state.msgs_to.length,"")
						if(this.$store.state.msgs_to.length>0){
							let a = 0
							for(var i=0;i<this.$store.state.msgs_to.length;i++){
								if(this.$store.state.msgs_to[i].fromOpId == json.fromOpId){
									this.$store.state.msgs_to[i] = json
									break;
								}else{
									a = 1
								}
								
							}
							if(a==1){
								this.$store.state.msgs_to.push(json)
							}
							
							
						}else{
							this.$store.state.msgs_to.push(json)  
						}
							 
						//console.log(this.$store.state.msgs_to,"22222")
//							if(this.$store.state.msgs.length>0){
//								
//								for(var i=0;i<this.$store.state.msgs.length;i++){
//									console.log(this.$store.state.msgs[i][0].fromOpId==json.fromOpId," "+this.$store.state.msgs[i][0].fromOpId,"--"+json.fromOpId)
//									if(this.$store.state.msgs[i][0].fromOpId==json.fromOpId){
//										this.$store.state.msgs[i].push(json)
//										break;
//									}else{
//										var new_a=[];
//										new_a.push(json);
//										this.$store.state.msgs.push(new_a)
//										break;
//									}
//								}
//							}else{
//								var new_array=[];
//								new_array.push(json)
//								this.$store.state.msgs.push(new_array)
//							}
							
							console.log(this.$store.state.msgs_to,"")
							break;
							
						case 'fans'://
							this.$store.state.fans.push(json.message)
							console.log(this.$store.state.fans,"")
							break;
							
						case 'friend'://
							if(this.$store.state.is_login){
								this.$store.state.friends.push(json)
							}else{
								this.$store.state.friends_to=json;
							}
							
							break;
							
						case 'system'://
							this.$store.state.systems.push(json.message)
							break;
							
						case 'like'://
							this.$store.state.likes.push(json.message)
							console.log(this.$store.state.likes,"")
							break;
							
						case 'discuss'://
							this.$store.state.discuss.push(json.message)
							console.log(this.$store.state.discuss,"")
							break;
							
						case 'reward'://
							this.$store.state.rewards.push(json.message)
							console.log(this.$store.state.rewards,"")
							break;
							
						case 'complaintSuggestion'://
							this.$store.state.complaintSuggestions.push(json.message)
							break;
					}

				} else {

					console.log(received_msg)
				}
			}
		}
	}
</script>

<style scoped="scoped">
	/**/
	#app{
		width: 100%;height: 13.3rem;
		position: relative;z-index: 300;
	    background: white;
	}
	.back {
		width: 1.8rem;
		height: 1rem;
		margin-left: 0.25rem;
	}
	
	.back img {
		width: 0.5rem;
		height: 0.5rem;
		float: left;
		margin-top: 0.425rem
	}
	
	.back p {
		font-size: 0.4rem;
		margin-left: 0.1rem;
		float: left;
		line-height: 1.35rem;
	}
	/**/
	
	.title {
		position: absolute;
		left: 0.4rem;
		margin-top: 0.28rem;
	}
	
	.title_p {
		font-size: 0.8rem;
		font-weight: bold;
		color: rgba(51, 51, 51, 1);
		font-family: castellar;
	}
	
	.title_tip {
		font-size: 0.35rem;
		color: #D0D0D0;
		position: relative;
		
	}
	
	.phone {
		position: absolute;
		top: 3.75rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
	}
	
	.phone p {
		width: 20%;
		float: left;
		font-size: 0.4rem;
		line-height: 1rem;
		font-family: SimHei;
		color: rgba(51, 51, 51, 1);
	}
	
	.phone input {
		width: 80%;
		height: 100%;
		border: 0;
		text-align: left;
		float: right;
		outline: none;
		/**/
		font-size: 0.32rem;
	}
	
	input::-webkit-input-placeholder {
		/* WebKit browsers */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input:-moz-placeholder {
		/* Mozilla Firefox 4 to 18 */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input::-moz-placeholder {
		/* Mozilla Firefox 19+ */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input:-ms-input-placeholder {
		/* Internet Explorer 10+ */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	.password {
		position: absolute;
		top: 4.9rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
	}
	
	.password p {
		width: 20%;
		float: left;
		line-height: 1rem;
		font-size: 0.425rem;
		font-family: SimHei;
		color: rgba(51, 51, 51, 1);
	}
	
	.password input {
		width: 80%;
		height: 100%;
		border: 0;
		outline: none;
		/**/
		text-align: left;
		float: right;
		font-size: 0.4rem;
	}
	
	.sumbit {
		width: 80%;
		height: 1rem;
		border-radius: 0.1rem;
		background-color: #000000;
		position: absolute;
		top: 6.8rem;
		margin: 0 10%;
	}
	
	.sumbit p {
		text-align: center;
		color: #fff;
		font-size: 0.425rem;
		margin-top: 0.2rem;
	}
	.down {
		position: absolute;
    	top: 4.55rem;
		width: 100%;
		height: 0.01rem;
		background: rgba(247, 247, 247, 1);
		margin-top: 0.3rem;
	}
	.new_down {
		position: absolute;
    	top: 5.6rem;
		width: 100%;
		height: 0.01rem;
		background: rgba(247, 247, 247, 1);
		margin-top: 0.3rem;
	}
	.forget{
		position: absolute;
    	top: 6rem;
    	font-size: 0.3rem;
    	right: 0.1rem;
    	color: #D0D0D0;
	}
</style>


