<template>
	<div id="app">
		<div @touchstart="backTo" class="back">
			<img src="../../assets/img/zuo.png" />
			<p></p>
		</div>
		<div class="title">
			<p class="title_p"></p>
			<p class="title_tip"></p>
		</div>
		<div class="phone_adress">
			<p> (+86)</p>
			<img src="../../assets/img/come_icon.png" />
		</div>
		<div class="phone">
			<p></p>
			<input ref="input_name" type="tel" placeholder="" maxlength="11" />
		</div>
		<div class="password">
			<p></p>
			<input ref="input_password" type="password" placeholder="6-12" maxlength="12" />
		</div>
		<div class="code">
			<p></p>
			<input ref="input_code" type="number" placeholder="" maxlength="4" />
			<div class="xian"></div>
			<div class="getCode" >
				<p v-show="!show" :class="{time_blue:is_blue,time_red:is_red}">{{count}} s</p>
				<p v-show="show" @touchstart="getCode"></p>
			</div>
		</div>
		<div class="sumbit" @touchstart="next">
			<p></p>
		</div>
	</div>
</template>

<script>
	import md5 from 'js-md5';
	import { Toast } from 'mint-ui';
	import store from '../../vuex/store'
	export default {
		store,
		data() {
			return {
				show: true,
				timer: null,
				count: '',
				is_red:false,
				is_blue:true,
				
			}
		},
		methods: {
			backTo() {
//				window.location = '/#/home'
     this.$router.push({path: '../home'})
			},
			next() {
				//window.location = '/#/regiter_information'
				var reg = /^1[3|4|5|7|8][0-9]{9}$/; //111
				var reg_pass = /^(?=.*[A-Za-z])(?=.*\d)[A-Za-z\d]{6,12}$/; //6-12
				var phone = this.$refs.input_name.value.trim()
				var password = this.$refs.input_password.value.trim()

				var code = this.$refs.input_code.value.trim()
				if(phone != null && phone != "") {
					if(reg.test(phone)) {
						if(password != null && password != "") {
							if(reg_pass.test(password)) {
								var m_pass = md5(password);
								var params = "{\"memPhone\":\"" + phone + "\",\"memPwd1\":\"" + m_pass + "\",\"smsVerifCode\":\"" + code + "\",\"pid\":\"684589bb5b0e40059ceb41d71be23d8d\",\"rid\":\"631e32975df34e89a6ec9f58512b912f\",\"cpFlag\":\"0\",\"regEquipment\":\"WAP\"}";
								console.log(params,"");
								this.$http({
									url: this.$store.state.request_url+'/api/user/register.do',
									method: 'post',
									headers: {
										"content-type": "application/json;charset=UTF-8"
									},
									body: params,
									emulateJSON: false,
								}).then(function(response) {

									if(response.body.meta.res == "00000") {
										this.login(m_pass,phone);
										
									} else {
										/**/
										Toast({
											message: response.data.meta.msg,
											position: 'middle',
											duration: 2000
										});

									}

								}).catch(function(err) {
									console.log(err)
									/**/
									Toast({
										message: err.data.meta.msg,
										position: 'middle',
										duration: 2000
									});
								})
							} else {
								/**/
								Toast({
									message: '6-12,',
									position: 'middle',
									duration: 2000
								});

							}

						} else {
							/**/
							Toast({
								message: '',
								position: 'middle',
								duration: 2000
							});

						}
					} else {
						/**/
						Toast({
							message: '',
							position: 'middle',
							duration: 2000
						});

					}
				} else {
					/**/
					Toast({
						message: '',
						position: 'middle',
						duration: 2000
					});

				}

			},
			getCode() {
				var reg = /^1[3|4|5|7|8][0-9]{9}$/;
				var phone = this.$refs.input_name.value.trim();
				console.log(phone);
				if(reg.test(phone)) {
					
					/**/
					const TIME_COUNT = 30;
					if(!this.timer) {
						
						this.count = TIME_COUNT;
						this.show = false;
						this.timer = setInterval(() => {
							if(this.count > 0 && this.count <= TIME_COUNT) {	
								if(this.count<=10){
									this.is_blue=false;
									this.is_red=true;
								}else{
									this.is_blue=true;
									this.is_red=false;
								}
								this.count--;
							} else {
								this.show = true;
								clearInterval(this.timer);
								this.timer = null;
							}
						}, 1000)
					}
					
					var params = "{\"memPhone\":\"" + phone + "\",\"msgType\":\"1\",\"msgLen\":\"4\"}";
					this.$http({
						url: this.$store.state.request_url+'/api/user/getSmsMsg.do',
						method: 'post',
						headers: {
							"content-type": "application/json;charset=UTF-8"
						},
						body: params,
						emulateJSON: false,
					}).then(function(response) {
						console.log(response);
					}).catch(function(err) {
						console.log(err)
					})
				} else {
					/**/
					Toast({
						message: '',
						position: 'middle',
						duration: 2000
					});

				}
			},
			login(pass,phone){
				var params = "{\"loginType\":\"2\",\"loginAccount\":\"" + phone + "\",\"loginPwd\":\"" + pass + "\",\"pid\":\"684589bb5b0e40059ceb41d71be23d8d\",\"rid\":\"631e32975df34e89a6ec9f58512b912f\",\"cpFlag\":\"0\",\"regEquipment\":\"WAP\"}";
								
				this.$http({
					url: this.$store.state.request_url+'/api/user/login.do',
					method: 'post',
					headers: {
							"content-type": "application/json;charset=UTF-8"
							},
					body: params,
					emulateJSON: false,
					}).then(function(response) {

					if(response.body.meta.res === "00000") {
						
//						console.log("memId");
//						console.log(response.body.data)
//						this.$store.state.data = response.body.data;//
						
						                localStorage.memId=response.body.data.memId
                                        localStorage.tokEn=response.body.data.tokEn
                                        localStorage.info=response.body.data.info
						
//						this.connectWebSocket(response.body.data.memId);//
						
//						window.location = '/#/regiter_information'
						this.$router.push({path: '../regiter_information'})
					}
				})
			},
			/**/
			connectWebSocket(id){
				//  web socket

				this.ws = new WebSocket("ws://120.76.188.131:7777?opId="+id);
			
				/**/
				if(this.ws.readyState != 1) {

				console.log(this.ws)
				
				var that = this
				/**/
				this.ws.onopen = function() {
					that.$store.state.new_ws=that.ws;
					that.ws.onmessage = that.getMessage; /**/
					}
				}

				/**/
				this.ws.onerror = function() {
					alert("...");
				}
			},
			/**/
			getMessage(evt) {
				var received_msg = evt.data;

				if(received_msg != "," && received_msg != "") {

					//jsonjson
					var json = eval('(' + received_msg + ')');
					console.log(json);
					
					switch(json.msgType){
						case 'msg'://
							this.$store.state.is_infro=true;
							this.$store.state.msgs.push(json)  
							console.log(this.$store.state.msgs_to.length,"")
						if(this.$store.state.msgs_to.length>0){
							let a = 0
							for(var i=0;i<this.$store.state.msgs_to.length;i++){
								if(this.$store.state.msgs_to[i].fromOpId == json.fromOpId){
									this.$store.state.msgs_to[i] = json
									break;
								}else{
									a = 1
								}
								
							}
							if(a==1){
								this.$store.state.msgs_to.push(json)
							}
							
							
						}else{
							this.$store.state.msgs_to.push(json)  
						}
							break;
							
						case 'fans'://
							this.$store.state.fans.push(json.msgType)
							break;
							
						case 'friend'://
							if(this.$store.state.is_login){
								this.$store.state.friends.push(json)
							}else{
								this.$store.state.friends_to=json;
							}
							break;
							
						case 'system'://
							this.$store.state.systems.push(json.msgType)
							break;
							
						case 'like'://
							this.$store.state.likes.push(json.msgType)
							break;
							
						case 'discuss'://
							this.$store.state.discuss.push(json.msgType)
							break;
							
						case 'reward'://
							this.$store.state.rewards.push(json.msgType)
							break;
							
						case 'complaintSuggestion'://
							this.$store.state.complaintSuggestions.push(json.msgType)
							break;
					}

				} else {

					console.log(received_msg)
				}
			}
		}
	}
</script>

<style scoped="scoped">
	#app{
		width: 100%;
		height: 13.3rem;
		position: relative;
		z-index: 300;
		background: white;
	}
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	
	input,
	textarea {
		outline: none;
	}
	/**/
	
	.back {
		width: 1.8rem;
		height: 1.25rem;
		margin-left: 0.25rem;
	}
	
	.back img {
		width: 0.4rem;
		height: 0.5rem;
		float: left;
		margin-top: 0.425rem
	}
	
	.back p {
		font-size: 0.4rem;
		margin-left: 0.1rem;
		float: left;
		line-height: 1.3rem;
	}
	/**/
	
	.title {
		position: absolute;
		left: 0.4rem;
		margin-top: 0.5rem;
	}
	
	.title_p {
		font-size: 0.8rem;
		font-weight: bold;
		color: rgba(51, 51, 51, 1);
		font-family: castellar;
	}
	
	.title_tip {
		font-size: 0.35rem;
		color: #D0D0D0;
	}
	
	.phone_adress {
		position: absolute;
		top: 3.7rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
		border-bottom: solid 1px #D0D0D0;
	}
	
	.phone_adress p {
		width: 2rem;
		float: left;
		font-size: 0.4rem;
		font-family: SimHei;
		line-height: 1rem;
		color: rgba(51, 51, 51, 1);
	}
	
	.phone_adress img {
		margin-top: 0.3rem;
		float: right;
		margin-right: 0.3rem;
		width: 0.4rem;
		height: 0.4rem;
	}
	
	.phone {
		position: absolute;
		top: 4.75rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
		border-bottom: rgba(247, 247, 247, 1) solid 5px;
	}
	
	.phone p {
		width: 20%;
		float: left;
		font-size: 0.4rem;
		line-height: 1rem;
		font-family: SimHei;
		color: rgba(51, 51, 51, 1);
	}
	
	.phone input {
		width: 80%;
		height: 100%;
		border: 0;
		text-align: left;
		float: right;
		outline: none;
		/**/
		font-size: 0.32rem;
	}
	
	input::-webkit-input-placeholder {
		/* WebKit browsers */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input:-moz-placeholder {
		/* Mozilla Firefox 4 to 18 */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input::-moz-placeholder {
		/* Mozilla Firefox 19+ */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	input:-ms-input-placeholder {
		/* Internet Explorer 10+ */
		color: #D0D0D0;
		font-size: 0.32rem;
	}
	
	.password {
		position: absolute;
		top: 5.9rem;
		width: 90%;
		left: 0.4rem;
		height: 1rem;
		border-bottom: rgba(247, 247, 247, 1) solid 5px;
	}
	
	.password p {
		width: 20%;
		float: left;
		line-height: 1rem;
		font-size: 0.425rem;
		font-family: SimHei;
		color: rgba(51, 51, 51, 1);
	}
	
	.password input {
		width: 80%;
		height: 100%;
		border: 0;
		outline: none;
		/**/
		text-align: left;
		float: right;
		font-size: 0.4rem;
	}
	
	.code {
		position: absolute;
		top: 7rem;
		width: 90%;
		left: 0.4rem;
		height: 1.25rem;
		border-bottom: rgba(247, 247, 247, 1) solid 5px;
	}
	
	.code p {
		width: 20%;
		float: left;
		font-size: 0.425rem;
		font-family: SimHei;
		line-height: 1.25rem;
		
	}
	
	.code input {
		width: 35%;
		height: 70%;
		border: 0;
		font-size: 0.4rem;
		margin-top: 0.15rem;
		outline: none;
		/**/
		margin-left: 7%;
		float: left;
	}
	
	.xian {
		float: left;
		width: 4px;
		height: 40%;
		margin-top: 0.425rem;
		background-color: #2E3135;
	}
	
	.getCode {
		width: 30%;
		float: right;
	}
	
	.getCode p {
		width: 100%;
		line-height: 1.25rem;
		font-size: 0.375rem;
		font-family: SimHei;
		
	}
	
	.time_blue{
		text-align: center;
		color: green;
	}
	.time_red{
		text-align: center;
		color: red;
	}
	.sumbit {
		width: 80%;
		height: 1rem;
		border-radius: 0.1rem;
		background-color: #000000;
		position: absolute;
		top: 9rem;
		margin: 0 10%;
	}
	
	.sumbit p {
		text-align: center;
		color: #fff;
		font-size: 0.475rem;
		margin-top: 0.2rem;
	}
</style>


