<template>
	<div id="app">
		<div class="message_big">

			<div class="head">
				<img src="../assets/img/zuo.png" class="back" @touchstart="back" />
				<p>{{$store.state.chat_item.fromNiceName}}</p>
				<img src="../assets/img/share_icon.png" class="share"/>
			</div>
			<ul id="mes_id" style="overflow-y:scroll;">
				<li v-for="chat in chatContent">
					<div @click="to_personal_data(chat)" class="tou" :class="{fr:chat.fromOpId==$store.state.data.memId,fl:chat.fromOpId!=$store.state.data.memId}">
						<img :src="chat.fromOpId==$store.state.data.memId?$store.state.chat_item.fromOpPic:pic" />
					</div>
					<div :class="{fr:chat.fromOpId==$store.state.data.memId,fl:chat.fromOpId!=$store.state.data.memId}">
						<!--<img :src="chat.fromOpId!=$store.state.data.memId?'../../static/img/witch_icon.png':'../../static/img/black_icon.png'" :class="{fr_icon:chat.fromOpId==$store.state.data.memId,fl_icon:chat.fromOpId!=$store.state.data.memId}" />-->
						<p>{{chat.message}}</p>
					</div>
				</li>
			</ul>
		</div>
		<div class="dia">
			<div class="footer">
				<div class="biaoqing">
					<img src="../assets/img/happy_icon.png" />
				</div>
                  
				<textarea type="text" v-model="content" placeholder="..." ref="mes" maxlength="1000"></textarea>
				<div class="send" @touchstart="sendMessage">
					<p></p>
				</div>
			</div>
		</div>
	</div>
</template>
<script>
	import { Toast } from 'mint-ui';
	//	import io from 'socket.io-client';
//	import web from '../vuex/webSocket.js'
	import store from '../vuex/store.js'
	export default {
		store,
		data() {
			return {
				is_my: false,
				is_other: true,
			
				id: "",
				ws: null, //websocket
				content:"",
				chatContent: [],
				pic:'',//
				names:'',
				message: {}
			}
		},

		methods: {
			to_personal_data(i){
				this.$store.state.Author_s_detailed_routing = 5
				this.$store.state.worksOpId = i.fromOpId
				
				this.$router.push({
						path: '../personal_data'
						});
			},
			
			back() {
				if(this.$store.state.chat_tiao ==0){
					  this.$router.push({
							path: '../message/chat'
						})
				}else if(this.$store.state.chat_tiao ==1){
					this.$router.push({
							path: '../My_friend'
						})
				}
				
				
				
				//window.location = '/#/message/chat'
			},
			sendMessage() {

				var mes = this.$refs.mes.value.trim();

				if(mes != "") {
					

//					var message = "{\"message\": \"" + mes + "\",\"toOpId\": \""+this.$store.state.chat_item.fromOpId+"\",\"fromOpId\": \""+this.$store.state.data.memId+"\",\"msgType\": \"msg\",\"fromOpPic\": \""+this.data.pic+"\"}";
					var message = "{\"message\": \"" + mes + "\",\"toOpId\": \""+this.$store.state.chat_item.fromOpId+"\",\"fromOpId\": \""+this.$store.state.data.memId+"\",\"msgType\": \"msg\",\"fromNiceName\": \""+this.names+"\",\"fromOpPic\": \""+this.pic+"\"}";					

					console.log(message,'555555',this.$store.state.data.memId)
					
					//
					/**/
					if(this.$store.state.new_ws.readyState != 1) {
  
						console.log(this.$store.state.new_ws)
				    
						var that = this
						/**/
						this.$store.state.new_ws.onopen = function() {
							
							console.log("lianjie");
							
							that.$store.state.new_ws.send(message);
							var json = eval('(' + message + ')');
							
							this.chatContent.push(json)
							
							this.content=""
						}
						
					}else{
						
						console.log(this.$store.state.new_ws,'11115555555555555555551',message);
						
						// Web Socket  send() 
						
						this.$store.state.new_ws.send(message);
						
						//this.chatContent.push(message)
						var json = eval('(' + message + ')');
							
						this.chatContent.push(json)
						
						this.content=""
						
					}
					
					/**/
					this.$nextTick(() => {
						var container = this.$el.querySelector("#mes_id");
						container.scrollTop = container.scrollHeight;
					})

				} else {
					/**/
					Toast({
						message: '',
						position: 'middle',
						duration: 2000
					});
				}
			},
			/**/
			connectWebSocket(id){
				//  web socket

				this.ws = new WebSocket("ws://120.76.188.131:7777?opId="+id);
				
			
				/**/
				if(this.ws.readyState != 1) {

				console.log(this.ws)
				
				var that = this
				/**/
				this.ws.onopen = function(){
					console.log("",that.ws)
					that.$store.state.new_ws=that.ws;
					//that.ws.onmessage = that.getMessage; /**/
					}
				}
				/**/
				this.ws.onerror = function() {
					alert("...");
				}
				
			},
			/**/
			getMessage(evt) {
				var received_msg = evt.data;
				console.log(evt.data,"")
				if(received_msg != "," && received_msg != "") {

					//jsonjson
					var json = eval('(' + received_msg + ')');
					console.log(json);
					
					switch(json.msgType){
						case 'msg'://
							
							this.$store.state.is_infro=true;
							this.$store.state.msgs.push(json)  
							console.log(this.$store.state.msgs_to.length,"")
						if(this.$store.state.msgs_to.length>0){
							let a = 0
							for(var i=0;i<this.$store.state.msgs_to.length;i++){
								if(this.$store.state.msgs_to[i].fromOpId == json.fromOpId){
									this.$store.state.msgs_to[i] = json
									break;
								}else{
									a = 1
								}
								
							}
							if(a==1){
								this.$store.state.msgs_to.push(json)
							}
							
							
						}else{
							this.$store.state.msgs_to.push(json)  
						}
							 if(json.fromOpId==this.$store.state.chat_item.fromOpId){
								this.chatContent.push(json);
						/**/
					this.$nextTick(() => {
						var container = this.$el.querySelector("#mes_id");
						container.scrollTop = container.scrollHeight;
					})
							}
							
							
							console.log(this.$store.state.msgs_to,"")
							break;
							
						
					}

				} else {

					console.log(received_msg)
				}
			}
			
				
				
		
		
		},
		created() {
			this.$store.state.new_ws.onmessage=this.getMessage; 
			//this.connectWebSocket(this.$store.state.data.memId);
            console.log(this.$store.state.msgs,'111111111111111',this.$store.state.chat_item)
			this.pic = localStorage.opPic
			this.names = localStorage.nickname
			for(var i=0;i<this.$store.state.msgs.length;i++){
				if(this.$store.state.msgs[i].fromOpId==this.$store.state.chat_item.fromOpId){
					this.chatContent.push(this.$store.state.msgs[i]);
				}
			}
//			this.$store.state.chatContent.push(this.chatContent[this.chatContent.length-1])
			
		},

		mounted() {
			/**/
			var container = this.$el.querySelector("#mes_id");
			container.scrollTop = container.scrollHeight;
             
                    
		}
	}
</script>

<style scoped="scoped">
	input,
	textarea {
		outline: none;
	}
	#app{
		width: 100%;
		height: 13.3rem;
		position: relative;
		z-index: 300;
		background: white;
	}
	li {
		list-style-type: none;
	}
	
	* {
		font-family: "";
	}
	
	p,
	ul {
		margin: 0;
		padding: 0;
	}
	
	.fr {
		width: 70%;
		float: right;
	}
	
	.fl {
		width: 70%;
		float: left;
	}
	
	.head {
		height: 1rem;
		width: 100%;
	}
	
	.back {
		margin-top: 0.27rem;
		margin-left: 0.2rem;
		margin-right: 0.15rem;
		float: left;
		width: 0.5rem;
		height: 0.5rem;
		
	}
	
	.share {
		margin-top: 0.1rem;
		float: right;
		width: 0.9rem;
		height: 0.6rem;
	}
	
	.head p {
		width: 40%;
		float: left;
		font-size: 0.5rem;
		line-height: 1rem;
	}
	
	.message_big {
		width: 100%;
		position: absolute;
		top: 0;
		overflow-y: auto;
		height: 11.8rem;
		background: rgba(246, 246, 246, 1);
	}
	
	ul {
		width: 100%;
		height: 91.5%;
	}
	
	ul li {
		width: 100%;
		font-size: 0.3rem;
		overflow: hidden;
		margin: 0.2rem auto;
		word-break: break-all;
	}
	
	.tou {
		width: 1rem;
		height: 1rem;
		margin: 0.2rem;
	}
	
	.tou img {
		width: 100%;
		height: 100%;
		border-radius: 50%;
	}
	
	ul li p {
		word-break: break-all;
		/**/
		margin-top: 0.2rem;
		border-radius: 0.3rem;
	}
	
	.fl p {
		background-color: #fff;
		border: 0.2rem #fff solid;
		-moz-box-shadow: 0.05rem 0.1rem 0.2rem #A6A6A6;
		-webkit-box-shadow: 0.05rem 0.1rem 0.2rem #A6A6A6;
		box-shadow: 0.05rem 0.1rem 0.2rem #A6A6A6;
		margin: 0.3rem auto;
	}
	
	.fr p {
		color: #fff;
		background: rgba(0, 0, 0, 1);
		border: 0.2rem #000 solid;
		-moz-box-shadow: -0.05rem 0.1rem 0.2rem #A6A6A6;
		-webkit-box-shadow: -0.05rem 0.1rem 0.2rem #A6A6A6;
		box-shadow: -0.05rem 0.1rem 0.2rem #A6A6A6;
		margin: 0.3rem auto;
	}
	
	.fl_icon {
		width: 0.4rem;
		height: 0.5rem;
		float: left;
		position: relative;
		top: 0.45rem;
		left: -0.32rem;
	}
	
	.fr_icon {
		width: 0.4rem;
		height: 0.6rem;
		float: right;
		position: relative;
		top: 0.4rem;
		right: -0.3rem;
		/*-webkit-transform: rotateY(180deg);
            -moz-transform: rotateY(180deg);
            -o-transform: rotateY(180deg);
            -ms-transform: rotateY(180deg);
            transform: rotateY(180deg);*/
	}
	
	.dia {
		position: fixed;
		bottom: 0;
		z-index: 10;
		height: 1.5rem;
		background-color: #fff;
		width: 100%;
	}
	
	.dia p {
		height: 30%;
		line-height: 1rem;
		color: rgba(51, 51, 51, 1);
		font-size: 0.35rem;
		text-align: center;
	}
	
	.send {
		width: 18%;
		background-color: #000;
		height: 0.6rem;
		margin-top: 0.2rem;
		margin-right: 0.25rem;
		border-radius: 0.1rem;
		float: right;
	}
	
	.send p {
		color: #fff;
		padding: 0.1rem;
		line-height: 0.4rem;
		text-align: center;
		font-size: 0.3rem;
	}
	
	.biaoqing {
		float: left;
		width: 10%;
		height: 0.8rem;
		margin-left: 0.1rem;
		margin-top: 0.2rem;
	}
	
	.biaoqing img {
		width: 0.8rem;
		height: 0.7rem;
	}
	
	.footer {
		width: 100%;
		position: fixed;
		bottom: 0.2rem;
		z-index: 4;
		-moz-box-shadow: 0 0 0.3rem rgba(247, 247, 247, 1);
		/*  Firefox */
		box-shadow: 0 0 0.3rem rgba(247, 247, 247, 1);
	}
	
	.footer textarea {
		overflow: hidden;
		resize: none;
		outline: none;
		float: left;
		position: absolute;
		border: 0;
		width: 60%;
		font-size: 0.35rem;
		margin-top: 0.1rem;
		height: 80%;
		margin-left: 3%;
		color: rgba(180, 180, 180, 1);
		border: #D8D8D8 solid 1px;
		padding-left: 0.2rem;
		opacity: 0.01rem;
		border-radius: 0.15rem;
	}
	
	textarea::-webkit-input-placeholder {
		line-height: 0.8rem;
	}
	
	textarea::-moz-placeholder {
		line-height: 0.8rem;
	}
	
	textarea::-ms-input-placeholder {
		line-height: 0.8rem;
	}
</style>


